/**
 *
 * @param {{
    name: string
    isFolder: boolean
    isOpen: boolean
    isLightTheme?: boolean
 * }} [options]
 */
export function resolveIconName (options = {}) {
  const opt = Object.assign({}, {
    name: '',
    isFolder: false,
    isLightTheme: false,
    isOpen: false
  }, options)

  let id = ''

  if (opt.isFolder) {
    const folder = iconsdata.folderNames[opt.name] || iconsdata.folder
    const folderExpaned = iconsdata.folderNamesExpanded[opt.name] || iconsdata.folderExpanded
    id = opt.isOpen ? folderExpaned : folder
  } else {
    const exts = opt.name.split('.').slice(1)

    id = iconsdata.fileNames[opt.name]

    for (let i = 0; i < exts.length; i++) {
      const ext = exts.slice(i).join('.')
      id = id || iconsdata.fileExtensions[ext]
    }

    id = id || iconsdata.file
  }

  return iconsdata.iconDefinitions[id].iconPath
}
